.card__img {
  text-align: center;
  margin-bottom: 35px;
}

.card__img img {
  width: 160px;
  height: 134px;
}

.card__describe h2 {
  color: #060b35;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__describe p {
  color: #616070;
  margin-top: 4px;
